.container {
  max-width: 480px;
  margin: auto;
  padding: 0 24px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 32px 0;
  display: flex;
}

.menu {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  display: flex;
}

.line {
  fill: none;
  stroke: #fff;
  stroke-width: 9px;
  transition: stroke-dasharray .9s cubic-bezier(.4, 0, .2, 1), stroke-dashoffset .9s cubic-bezier(.4, 0, .2, 1);
}

.line1 {
  stroke-dasharray: 60 207;
  stroke-width: 9px;
}

.line2 {
  stroke-dasharray: 60 60;
  stroke-width: 9px;
}

.line3 {
  stroke-dasharray: 60 207;
  stroke-width: 9px;
}

.opened .line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134px;
  stroke-width: 9px;
}

.opened .line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30px;
  stroke-width: 9px;
}

.opened .line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134px;
  stroke-width: 9px;
}

.mobile-menu {
  max-width: 327px;
  background-color: gray;
  border-radius: 8px;
  flex-direction: column;
  justify-content: left;
  margin: auto;
  display: flex;
}

.mobile-menu-item {
  padding: 24px;
}

.mobile-menu-item:nth-child(2) {
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}



.attribution {
  text-align: center;
  font-size: 11px;
}

.attribution a {
  color: #3e52a3;
}

body {
  box-sizing: border-box;
  background-image: url("image-hero-mobile.e7309e8a.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  background-attachment: fixed;
}

ul {
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none;
}

